<template>
    <div>
            <v-expansion-panels accordion flat class="qiong-td qiong-br-0 w-full qiong-ex-panels py-0"
                v-for="(v, i) in buy" :key="i"
                :class="{ 'qiong-td-zebra': i % 2 == 0, 'qiong-td-light': i % 2 != 0 }"
            >
                <v-expansion-panel class="qiong-br-0">
                    <v-expansion-panel-header class="fs-12-vw  qiong-td-exp">
                        <div class="qiong-wide-7 qiong-td-fix">&nbsp;</div>
                        <div class="qiong-wide-15 fw-b">{{ v.order_code }}&nbsp;</div>
                        <div class="qiong-wide-11">{{ v.product.price * v.num }}</div>
                        <div class="qiong-wide-13">{{ v.users_permissions_user.number_code }}</div>
                        <div class="qiong-wide-26">{{ v.users_permissions_user.display_name }}</div>
                        <div class="qiong-wide-13">{{ view.ser_timed(v.buy_time) }}</div>
                        <div class="qiong-wide-6">{{ v.product.pv * v.num }}</div>
                        <div class="qiong-wide-9">
                            <qiong-icon-flat :icon="'mdi-chevron-down'" :color="'primary'"></qiong-icon-flat>
                        </div>
                    </v-expansion-panel-header>
                    <v-expansion-panel-content class="qiong-primary-shadow-inset">
                        
                        <div class="px-15">

                            <buys-records-every-product :item="v"></buys-records-every-product>
                            <br/>
                            <buys-records-every-msg :item="v"></buys-records-every-msg>

                            <buys-records-every-result :item="v"></buys-records-every-result>
                        </div>
                    </v-expansion-panel-content>
                </v-expansion-panel>
            </v-expansion-panels>
    </div>

</template>

<script>
import QiongIconFlat from '../../../../components/Qiong/Button/QiongIconFlat.vue'
import BuysRecordsEveryMsg from '../Data/BuysRecordsEveryMsg.vue'
import BuysRecordsEveryProduct from '../Data/BuysRecordsEveryProduct.vue'
import BuysRecordsEveryResult from '../Data/BuysRecordsEveryResult.vue'
    export default {
  components: { QiongIconFlat, BuysRecordsEveryProduct, BuysRecordsEveryMsg, BuysRecordsEveryResult },
        props: {
            buy: {
                type: Array
            }
        },
        mounted() {
            
        },
        name: '',
        data() {
            return {
                
            }
        }
    }
</script>

<style lang="sass" scoped>
.qiong-td-fix
    height: 3.5vw
</style>


