<template>
    <div class="qiong-td qiong-product-result qiong-sub_header">
        <div class="qiong-wide-22">
            收货人信息:&nbsp;{{ item.buyer_named }}
            <br/>

            <div class="qiong-pt-5">付款方式:&nbsp;{{ view.backend.view_pay_way(item.pay_way) }}</div>
        </div>
        <div class="qiong-wide-28">
            {{ item.buyer_email }}
            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            {{ item.buyer_phoned }}
            <br/>

            <div class="qiong-pt-5">
                {{ view.ser_timed(item.buy_time, true, true) }}
            </div>
        </div>
        <div class="qiong-wide-30 text-right">
            &nbsp;
        </div>
        <div class="qiong-wide-8 text-right fs-24-vw fw-b qiong-next_header">
            结算
            <br/>
            <div class="qiong-pt-7 qiong-txt-primary-sub">IPV</div>
        </div>
        <div class="qiong-wide-12 text-right fs-24-vw fw-b qiong-next_header" >
            HK${{ item.num * item.product.price }}
            <br/>
            <div class="qiong-pt-7 qiong-txt-primary-sub">{{ item.num * item.product.pv }}</div>
        </div>
    </div>
</template>

<script>
    export default {
        props: {
            item: {
                type: Object,
            }
        },
        name: '',
        data() {
            return {
                
            }
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


