<template>
    <div class="qiong-td w-full">
        <div class="qiong-wide-8">
            <qiong-image :src="conf.baseURL + item.product.avatar"></qiong-image>
        </div>
        <div class="qiong-wide-30 qiong-pt-15 qiong-pl-7">
            <span class="fs-30-vw fw-b qiong-txt-primary qiong-txt-shadow">
                {{ item.product.named }}
            </span>
            <br/>
            <div class="fs-15-vw pt-1 qiong-txt-primary-sub qiong-blur-1">
                数量:&nbsp;{{ item.num }}&nbsp;&nbsp;&nbsp;&nbsp;SKU:&nbsp;{{ item.product.sku }}
            </div>
        </div>
        <div class="qiong-wide-25 qiong-sub_header qiong-pt-15 mt-3 fs-16-vw ">
            价格/件:&nbsp;{{ item.product.price }}
        </div>
        <div class="qiong-wide-20 qiong-sub_header qiong-pt-15 mt-3 fs-16-vw ">
            Pv:&nbsp;{{ item.product.pv }}
        </div>
        <div class="qiong-wide-12 qiong-sub_header qiong-pt-15 mt-3 fs-16-vw ">
            总价:&nbsp;{{ item.product.pv * item.num }}
        </div>
    </div>
</template>

<script>
import QiongImage from '../../../../components/Qiong/Ui/QiongImage.vue'
    export default {
  components: { QiongImage },
        props: {
            item: {
                type: Object
            }
        },
        name: '',
        data() {
            return {
                
            }
        },
        mounted() {
            console.log(this.item)
        },
    }
</script>

<style lang="sass" scoped>
    
</style>


